<template>
  <div>
    <m-form
        title=" "
        :showList="false"
        :listRoute="{name:'PersonnelList'}"
        :exportBaseUrl="printUrl"
        :exportParams="{id: item.id}"
        :canDelete="false"
        :canSubmit="canSubmit"
        :show-navigation-btns="false"
        :isEditing.sync="isEditing"
        :show-submit-and-clear-btn="false"
        :show-actions="!item.verified"
        @submit="submit"
        @delete="deleteItem"
        @clearForm="clearForm()"
        ref="workshopForm"

    >

      <template>
        <v-row>
          <v-col cols="12" md="3">
            <v-text-field label="* " v-on:keypress="NoneNumbersOnly" :rules="[rules.required,]" v-model="item.name" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*  " v-on:keypress="NoneNumbersOnly" :rules="[rules.required,]"  v-model="item.last_name" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*  " v-on:keypress="NoneNumbersOnly" :rules="[rules.required,]" v-model="item.father_name" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label=" " v-on:keypress="NumbersOnly" v-model="item.personnel_code" background-color="white" :disabled="true">
            </v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                :rules="[rules.required,]"
                label="* "
                :items="NATIONALITY_TYPE"
                v-model="item.nationality"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-if="item.nationality == 1" label="* " v-model="iran" background-color="white" :disabled="true"/>
            <v-autocomplete
                v-if="item.nationality !== 1"
                :rules="[rules.required,]"
                label="* "
                :items="Country"
                v-model="item.country"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />

          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                :rules="[rules.required,]"
                label="* "
                :items="GENDER_TYPE"
                v-model="item.gender"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3" v-if="item.nationality !== 2">
            <v-text-field v-show="false" v-if="item.gender == 'f'" label="*  " v-model="item.military_service = 'x'" background-color="white" :disabled="true"/>
            <v-text-field v-show="item.gender == 'f'" label="*  " v-model="female" background-color="white" :disabled="true"/>
            <v-autocomplete
                :rules="[rules.required,]"
                v-if="item.gender !== 'f' "
                label="*  "
                :items="MILITARY_SERVICE_STATUS"
                v-model="item.military_service"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3" v-if="item.nationality == 2">
            <v-text-field v-on:keypress="NumbersOnly" :rules="[rules.required,]" label="*   "  v-model="item.national_code" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3" v-if="item.nationality !== 2">
            <v-text-field v-on:keypress="NumbersOnly" :rules="[rules.required,]" label="*  " counter v-model="item.identity_code" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3" v-if="item.nationality !== 2">
            <v-text-field v-on:keypress="NumbersOnly" :rules="[rules.required,]" label="*  "  v-model="item.national_code" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                :rules="[rules.required,]"
                label="*  "
                :items="MARITAL_STATUS_TYPES"
                v-model="item.marital_status"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3" v-if="item.id">
            <v-text-field label="  " v-model="item.get_child_number"  background-color="white" :disabled="true"/>
          </v-col>
          <v-col cols="12" md="3">
            <date v-model="item.date_of_birth" label="*  " :default="false" :disabled="!isEditing" />
          </v-col>
          <v-col cols="12" md="3" v-if="item.nationality !== 2">
            <date v-model="item.date_of_exportation" label="*   " :default="false" :disabled="!isEditing" />
          </v-col>
          <v-col cols="12" md="2" v-if="item.nationality !== 2">
            <city-select  v-if="item.nationality !== 2" label=" *   "
                         v-model="item.location_of_birth"  background-color="white"
                         :disabled="!isEditing" :rules="[rules.required,]"></city-select>
          </v-col>
          <v-col cols="12" md="3" v-if="item.nationality == 2">
            <v-text-field v-if="item.nationality == 2"
                          v-on:keypress="NoneNumbersOnly"
                          label="*  "
                          v-model="item.location_of_foreign_birth"
                          :rules="[rules.required,]"
                          background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2" v-if="item.nationality !== 2">
            <city-select label="*   "
                         v-model="item.location_of_exportation"
                         background-color="white" :disabled="!isEditing"
                         :rules="[rules.required,]"></city-select>
          </v-col>
          <v-col cols="12" md="2" v-if="item.nationality !== 2">
            <city-select  label="  " v-model="item.sector_of_exportation"   background-color="white" :disabled="!isEditing"></city-select>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field label="  " v-model="item.phone_number"   background-color="white" :disabled="!isEditing" v-on:keypress="NumbersOnly" />
          </v-col>
          <v-col cols="12" md="1">
            <v-text-field label=" " v-model="item.city_phone_code"   background-color="white" :disabled="!isEditing" v-on:keypress="NumbersOnly" />
          </v-col>
          <v-col cols="12" md="2">
            <city-select label="*   " v-model="item.city"  background-color="white" :disabled="!isEditing" :rules="[rules.required,]"></city-select>
          </v-col>
          <v-col cols="12" md="5">
            <v-text-field label="*  " v-model="item.address"   background-color="white" :disabled="!isEditing"  :rules="[rules.required,]"/>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field label="*   " v-model="item.postal_code"   background-color="white" :disabled="!isEditing" v-on:keypress="NumbersOnly" :rules="[rules.required,]"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*   1" v-model="item.mobile_number_1" background-color="white" :disabled="!isEditing" v-on:keypress="NumbersOnly" :rules="[rules.required,]"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="   2 " v-model="item.mobile_number_2" background-color="white" :disabled="!isEditing" v-on:keypress="NumbersOnly" />
          </v-col>
          <v-col cols="12" md="2">
            <v-switch
                @change="item.insurance_code = null"
                v-model="item.insurance"
                label= '   '
                :disabled="!isEditing"
            ></v-switch>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-on:keypress="NumbersOnly"
                          :rules="[rules.required,]"
                          label="  " v-model="item.insurance_code"
                          background-color="white"
                          :disabled="!item.insurance || !isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                @change="item.field_of_study = null"
                :rules="[rules.required,]"
                label="*  "
                :items="DEGREE_TYPE"
                v-model="item.degree_education"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
                v-on:keypress="NoneNumbersOnly"
                :rules="[rules.required,]"
                label="*  "
                v-model="item.field_of_study"
                background-color="white"
                :disabled="!isEditing || item.degree_education == 1 || item.degree_education == 2 "/>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                :rules="[rules.required,]"
                label="*  "
                :items="UNIVERSITY_TYPES"
                v-model="item.university_type"
                item-text="name"
                item-value="value"
                :disabled="!isEditing || item.degree_education == 1 || item.degree_education == 2 || item.degree_education == 3 "/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*  "
                          v-on:keypress="NoneNumbersOnly"
                          :rules="[rules.required,]"
                          v-model="item.university_name"
                          background-color="white"
                          :disabled="!isEditing || item.degree_education == 1 || item.degree_education == 2 || item.degree_education == 3 "/>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                v-on:keypress="NoneNumbersOnly"
                :rules="[rules.required,]"
                label="*  "
                :items="BANK_NAMES"
                v-model="item.account_bank_name"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"/>

          </v-col>
          <v-col cols="12" md="3">

            <v-text-field :rules="[rules.required,]" v-on:keypress="NumbersOnly" label="*   " v-model="item.account_bank_number"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <cart :rules="[rules.max_cart, rules.min_cart, rules.required]"
                  v-on:keypress="NumbersTo16Only"
                  ref="cart"
                  label="*   "
                  v-model="item.bank_cart_number"
                  :disabled="!isEditing">

            </cart>
          </v-col>
          <v-col cols="12" md="3">
            <cart :rules="[rules.max_sheba, rules.min_sheba, rules.required]"
                  v-on:keypress="NumbersTo24Only"
                  ref="sheba"
                  label="*  " v-model="item.sheba_number"
                  :disabled="!isEditing"
                  append-icon="IR">
            </cart>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                :rules="[rules.required,]"
                label="*   "
                :items="PERSONNEL_STATUS"
                v-model="item.is_personnel_active"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>

        </v-row>
      </template>
      <v-btn
          class="light-blue white--text mt-6  mr-2 float-left"
          @click="verifyPersonnel(item.id)"
          v-if="item.id && !item.verified" > </v-btn>
      <v-btn
          class="red white--text mt-6  mr-2 float-left"
          @click="verifyUnPersonnel(item.id)"

          v-if="item.id && item.verified" >    </v-btn>

    </m-form>

  </div>

</template>

<script>

import TenderList from "@/modules/contracting/tender/TenderList";
import {MFormMixin} from "@/components/m-form";
import mtime from "@/components/mcomponents/cleave/Time";
import FormsMixin from "@/mixin/forms";
import FactorMixin from "@/views/panel/factor/mixin";
import TreeSelect from "@/components/selects/TreeSelect";
import {PathLevels, VisitorLevels} from "@/variables";
import citySelect from "@/components/selects/CitySelect";
import MDatatable from "@/components/m-datatable";
import formsMixin from "@/mixin/forms";
import money from "@/components/mcomponents/cleave/Money";
import date from "@/components/mcomponents/cleave/Date";
import cart from "@/components/scomponents/Cart";


import TransactionForm from "@/views/panel/transaction/Form";
import LadingMixin from "@/modules/dashtbashi/LadingMixin";
import {range} from "lodash";


export default {
  name: "PersonnelForm",
  mixins: [MFormMixin, LadingMixin, formsMixin, FormsMixin, FactorMixin],
  components: {mtime, TreeSelect, citySelect, TenderList, MDatatable, TransactionForm, money, cart},
  props: {
    id: {},
  },
  data() {
    return {
      NATIONALITY_TYPE: [
        {name: ' ', value: 1},
        {name: ' ', value: 2},
      ],
      PERSONNEL_STATUS: [
        {name: ' ', value: true},
        {name: ' ', value: false},
      ],
      GENDER_TYPE: [
        {name: ' ', value: 'm'},
        {name: '', value: 'f'},
      ],
      MILITARY_SERVICE_STATUS: [
        {name: '  ', value: 'd'},
        {name: ' ', value: 'n'},
        {name: '', value: 'e'},
        {name: ' ', value: 'x'},
      ],
      MARITAL_STATUS_TYPES: [
        {name: ' ', value: 's'},
        {name: '', value: 'm'},
        {name: ' ', value: 'c'},
      ],
      DEGREE_TYPE: [
        {name: '  ', value: 1},
        {name: '  ', value: 2},
        {name: ' ', value: 3},
        {name: '', value: 4},
        {name: '', value: 5},
        {name: ' ', value: 6},
        {name: '', value: 7},
        {name: ' ', value: 8},
      ],

      BANK_NAMES: [
        {name: ' ', value: 'BAYAN'},
        {name: ' ', value: 'CHART'},
        {name: '  ', value: 'BEGHTE'},
        {name: '   ', value: 'ZAMIN'},
        {name: ' ', value: 'BPARSI'},
        {name: ' ', value: 'BPASAR'},
        {name: '  ', value: 'BPOST'},
        {name: '    ', value: 'BEDIRA'},
        {name: '  ', value: 'BTEJAR'},
        {name: '    ', value: 'EURO'},
        {name: '   ', value: 'ESLA'},
        {name: '  ', value: 'BCDEVE'},
        {name: '  ', value: 'KHAVA'},
        {name: ' ', value: 'BDAY'},
        {name: '   ', value: 'BREFAH'},
        {name: '  ', value: 'BSAMAN'},
        {name: '  ', value: 'BSARMA'},
        {name: '  ', value: 'BSEPAH'},
        {name: '  ', value: 'BSINA'},
        {name: ' ', value: 'BCITY'},
        {name: '    ', value: 'BINDMI'},
        {name: '  ', value: 'BSADER'},
        {name: '  ()', value: 'FUTU'},
        {name: '    ', value: 'BRESALA'},
        {name: '    ', value: 'BGHARZ'},
        {name: ' ', value: 'BKARAF'},
        {name: ' ', value: 'BKESHA'},
        {name: '  ', value: 'BTOURI'},
        {name: ' ', value: 'BMASKA'},
        {name: ' ', value: 'BMELLA'},
        {name: '   ', value: 'BMELLI'},
        {name: '   -  ', value: 'VENE'},
        {name: '    ', value: 'CASP'},
        {name: '     ', value: 'TOSE'},
        {name: '     ', value: 'MELAL'},
        {name: '    ', value: 'NOR'},
      ],
      UNIVERSITY_TYPES: [
        {name: ' ', value: 'st'},
        {name: '', value: 'op'},
        {name: ' ', value: 'np'},
        {name: ' ', value: 'el'},
        {name: ' ', value: 'pa'},
        {name: '    ', value: 'pr'},
      ],
      Country: [
        {name: ' ', value: ''},
        {name: '', value: ''},
        {name: '', value: ''},
      ],

      testt: '',
      printUrl: 'payroll/personnel/all',
      isWorkshopConfirmed: false,
      worshopId: 0,
      baseUrl: "payroll/personnel",
      permissionBasename: "personnel",
      appendSlash: true,
      hasList: false,
      hasIdProp: true,
      hasLock: false,
      isDefinable: false,
      myClass: '',
      factors: [],
      cart_number: '################',
      PathLevels,
      VisitorLevels,
      paymentDialog: false,
      payment: '',
      female: ' ',
      iran: '',
      performClearForm: true,
      rules: {
        required: value => !!value || 'Required.',
        min: v => v.length >= 8 || 'Min 8 characters',
        max_cart: v => v.length <= 19 || 'Max 19 characters',
        max_sheba: v => v.length <= 29 || 'max 29 characters',
        min_cart: v => v.length >= 19 || 'Min 19 characters',
        min_sheba: v => v.length >= 29 || 'Min 29 characters',
      },
    };
  },
  computed: {
    headers() {
      return [
        {
          text: "",
          value: "name",
        },
        {
          text: " ",
          value: "last_name",
        },
        {
          text: " ",
          value: "father_name",
        },
        {
          text: " ",
          value: "personnel_code",
        },
        {
          text: "",
          value: "nationality",
        },
        {
          text: "",
          value: "gender",
        },
        {
          text: " ",
          value: "military_service",
        },
        {
          text: " ",
          value: "identity_code",
        },

        {
          text: " ",
          value: "national_code",
        },
        {
          text: " ",
          value: "marital_status",
        },
        {
          text: " ",
          value: "number_of_childes",
        },
        {
          text: " ",
          value: "date_of_birth",
        },
        {
          text: "  ",
          value: "date_of_exportation",
        },
        {
          text: " ",
          value: "location_of_birth",
        },
        {
          text: "  ",
          value: "location_of_exportation",
        },
        {
          text: "  ",
          value: "sector_of_exportation",
        },
        {
          text: " ",
          value: "city_phone_code",
        },
        {
          text: " ",
          value: "phone_number",
        },
        {
          text: "",
          value: "address",
        },
        {
          text: " ",
          value: "postal_code",
        },
        {
          text: "  1",
          value: "mobile_number_1",
        },
        {
          text: "  2",
          value: "mobile_number_2",
        },
        {
          text: "  ",
          value: "insurance",
        },
        {
          text: " ",
          value: "insurance_code",
        },
        {
          text: " ",
          value: "degree_education",
        },
        {
          text: " ",
          value: "field_of_study",
        },
        {
          text: " ",
          value: "university_type",
        },
        {
          text: " ",
          value: "university_name",
        },
        {
          text: " ",
          value: "account_bank_name",
        },
        {
          text: "  ",
          value: "account_bank_number",
        },
        {
          text: "  ",
          value: "bank_cart_number",
        },
        {
          text: " ",
          value: "sheba_number",
        },
      ];

    },
  },
  mounted() {
    if (this.$route.params.id) {
      this.isEditing = false
    }
  },
  methods: {
    put_under_line(str) {
      this.testt = this.cardNumberSpace(str, 4, '-')
      for (let i in range(0, (this.testt.length) + 1)){
        this.cart_number = this.testt.replace('-', '')
        console.log(this.cart_number)
      }},
    cardNumberSpace: function spacify(str, after, c) {
      if (!str) {
        return '';
      }
      var v = str.replace(/[^\dA-Z]/g, ''),
          reg = new RegExp(".{" + after + "}", "g");
      return v.replace(reg, function (a) {
        return a + c;
      }).replace(/[^0-9]+$/, "");
    },

    NumbersOnly(evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault();;
      } else {
        return true;
      }
    },
    NumbersTo16Only(evt) {
      if (this.$refs.cart.$props.value.length >= 16){
        evt.preventDefault();
      }
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault();;
      } else {
        return true;
      }
    },
    NumbersTo24Only(evt) {
      if (this.$refs.sheba.$props.value.length >= 24){
        evt.preventDefault();
      }
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault();;
      } else {
        return true;
      }
    },
    NoneNumbersOnly(evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        return true;
      } else {
        evt.preventDefault();;
      }
    },

    verifyPersonnel(id){
      this.request({
        url: this.endpoint(`payroll/personnel/verify/` + id + '/'),
        method: "get",
        success: data => {
          console.log(data);
          this.notify('    ', 'success')
          window.location.reload();
        },
        error: data => {
          this.notify(data.response.data[0].messages[0], 'warning')

        }
      })

    },
    verifyUnPersonnel(id){
      this.request({
        url: this.endpoint(`payroll/personnel/unverify/` + id + '/'),
        method: "get",
        success: data => {
          console.log(data);
          this.notify('    ', 'success')
          window.location.reload();
        },
        error: data => {
          this.notify(data.response.data[0].messages[0], 'warning')

        }
      })

    },
    set(value){

    },
    to(item) {
      return {
        name: 'WorkshopDetail',
        params: {
          id: item.id,
        },
      };
    },

    unConfirm() {
      this.$router.go()
      this.notify('    ', 'warning')
    },
  },
}
</script>

<style scoped lang="scss"></style>

