<template>
  <div>
    <m-form
        title=" "
        :showList="false"
        :listRoute="{name:'TenderList'}"
        :exportBaseUrl="printUrl"
        :exportParams="{id: item.id}"
        :canDelete="false"
        :canSubmit="canSubmit"
        :isEditing.sync="isEditing"
        @submit="submit"
        @delete="deleteItem"
        @clearForm="clearForm()"
        ref="tenderFrom"

    >

      <template>
        <v-row>
          <v-col cols="12" md="2">
            <v-text-field label=" " v-model="item.code" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field label=" " v-model="item.title" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="6">
            <v-textarea label=" " v-model="item.explanation" :disabled="!isEditing"></v-textarea>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="2">
            <CitySelect label="  " v-model="item.province" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <CitySelect label="  " v-model="item.city" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="4">
            <v-autocomplete
                label="  "
                :items="classification"
                v-model="item.classification"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="2">
            <v-text-field label=" " v-model="item.bidder" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field label="   " v-model="item.bidder_postal_code" background-color="white"
                          :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="6">
            <v-textarea label="  " v-model="item.bidder_address" :disabled="!isEditing"></v-textarea>
          </v-col>
        </v-row>
        <v-row>

          <v-col cols="12" md="2">
            <date v-model="item.received_deadline" label="  " :default="true" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <date v-model="item.send_offer_deadline" label="    " :default="true"
                  :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <date v-model="item.opening_date" label="   " :default="true" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <date v-model="item.offer_expiration" label="  " :default="true" :disabled="!isEditing"/>
          </v-col>

        </v-row>
        <v-dialog v-model="paymentDialog">
          <transaction-form
              type="receivedGuarantee"
              :modal-mode="false"
              :contract-modal-mode="true"
              @submit="submit"
              ref="transactionForm"
          />
        </v-dialog>
      </template>

      <v-btn @click="confirmed(item)" v-if="item.id && !item.is_confirmed && !isTenderConfirmed && item.is_defined "
             class="blue mt-6 mr-2 float-left">
      </v-btn>
      <v-btn @click="unConfirm(item)" v-if="item.id && !item.is_confirmed && !isTenderConfirmed && item.is_defined"
             class="red mt-6  mr-4 float-left">
      </v-btn>
      <v-btn class="blue white--text mt-6 ml-2 float-left"
             v-if="item.is_confirmed || isTenderConfirmed && item.is_defined" @click="addContract(item)"> 
      </v-btn>

      <v-btn class="light-blue white--text mt-6 ml-2 float-left"
             v-if="item.is_confirmed || isTenderConfirmed && item.is_defined" @click="paymentDialog = true">  
        
      </v-btn>
    </m-form>
  </div>

</template>

<script>

import TenderList from "@/modules/contracting/tender/TenderList";
import {MFormMixin} from "@/components/m-form";
import mtime from "@/components/mcomponents/cleave/Time";
import FormsMixin from "@/mixin/forms";
import FactorMixin from "@/views/panel/factor/mixin";
import TreeSelect from "@/components/selects/TreeSelect";
import {PathLevels, VisitorLevels} from "@/variables";
import citySelect from "@/components/selects/CitySelect";
import MDatatable from "@/components/m-datatable";
import formsMixin from "@/mixin/forms";
import money from "@/components/mcomponents/cleave/Money";
import date from "@/components/mcomponents/cleave/Date";


import TransactionForm from "@/views/panel/transaction/Form";
import LadingMixin from "@/modules/dashtbashi/LadingMixin";


export default {
  name: "TenderForm",
  mixins: [MFormMixin, LadingMixin, formsMixin, FormsMixin, FactorMixin],
  components: {mtime, TreeSelect, citySelect, TenderList, MDatatable, TransactionForm, money},
  props: {
    id: {},
  },
  data() {
    return {
      printUrl: 'reports/tender/all',
      isTenderConfirmed: false,
      tenderId: 0,
      classification: [
        {name: '', value: 'w'},
        {name: '   ', value: 'spl'},
        {name: '   ', value: 'npl'},
        {name: '', value: 'c'},
        {name: '', value: 'o'},
      ],
      baseUrl: "contracting/tender",
      permissionBasename: "tender",
      appendSlash: true,
      hasList: false,
      hasIdProp: true,
      hasLock: true,
      isDefinable: true,
      myClass: '',
      factors: [],
      PathLevels,
      VisitorLevels,
      paymentDialog: false,
      payment: '',
      performClearForm: true,
    };
  },
  computed: {
    headers() {
      return [
        {
          text: "",
          value: "code",
          type: "numeric",

        },
        {
          text: "",
          value: "title",
        },
        {
          text: "",
          value: "explanation",
        },
        {
          text: "",
          value: "province",
        },

        {
          text: "",
          value: "city",
        },
        {
          text: " ",
          value: "bidder",
        },
        {
          text: "   ",
          value: "bidder_post",
          type: "numeric",

        },
        {
          text: "  ",
          value: "bidder_address",
        },


        {
          text: "  ",
          value: "received_deadline",
          type: "date",
        },
        {
          text: "    ",
          value: "send_offer_deadline",
          type: "date",
        },

        {
          text: "   ",
          value: "opening_date",
          type: "date",
        },

        {
          text: "  ",
          value: "offer_expiration",
          type: "date",
        },

      ];
    },
  },
  beforeDestroy() {
    if (this.$refs.transactionForm.item.id) {
      this.request({
        url: this.endpoint(`contracting/tender/transaction/` + this.$refs.tenderFrom.$props.exportParams.id + '/' + this.$refs.transactionForm.item.id + '/'),
        method: "get",
        success: data => {
          console.log(data)
          this.notify('   ', 'success')
        }
      })
    }
  },

  methods: {
    to(item) {
      return {
        name: 'TenderDetail',
        params: {
          id: item.id,
        },
      };
    },

    unConfirm() {
      this.$router.go()
      this.notify('   ', 'warning')
    },
    addContract(item) {
      this.$router.push('/panel/contract/' + '?tender=' + item.id)

    },
    confirmed(item) {
      this.request({
        url: this.endpoint(`contracting/tender/confirmed/` + item.id + '/'),
        method: "get",
        success: data => {
          this.notify('   ', 'success')
          this.isTenderConfirmed = true
        }
      })
    },
  },
}
</script>

<style scoped lang="scss"></style>

