                  v-model="item.is_template"
                  item-text="name"
                  item-value="value"
                  :disabled="!isEditing"
                  :rules="[rules.required,]"
              />
            </v-col>
            <v-col cols="12" md="3" v-if="item.is_template == 'p' && item.is_template != 't'">
              <v-autocomplete
                  v-if="!this.contract"
                  label="   "
                  :items="templates"
                  item-text="name"
                  item-value="info"
                  v-model="item"
                  @change="item.is_template = 'p' ; item.id = null ; item.name = null"
                  :disabled="!isEditing"
