                  :disabled="!isEditing"
              />
              <v-text-field
                  label="  "
                  v-if="this.contract"
                  disabled="true"
                  v-model="item.contract = this.contract"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="3">
