<script>

import TenderList from "@/modules/contracting/tender/TenderList";
import {MFormMixin} from "@/components/m-form";
import mtime from "@/components/mcomponents/cleave/Time";
import FormsMixin from "@/mixin/forms";
import FactorMixin from "@/views/panel/factor/mixin";
import TreeSelect from "@/components/selects/TreeSelect";
import {PathLevels, VisitorLevels} from "@/variables";
import citySelect from "@/components/selects/CitySelect";
import MDatatable from "@/components/m-datatable";
import formsMixin from "@/mixin/forms";
import money from "@/components/mcomponents/cleave/Money";
import date from "@/components/mcomponents/cleave/Date";


import TransactionForm from "@/views/panel/transaction/Form";
import LadingMixin from "@/modules/dashtbashi/LadingMixin";


export default {
  name: "HRLetterForm",
  mixins: [MFormMixin, LadingMixin, formsMixin, FormsMixin, FactorMixin],
  components: {mtime, TreeSelect, citySelect, TenderList, MDatatable, TransactionForm, money},
  props: {
    id: {},
  },
  data() {
    return {

      NATURE_TYPES: [
        {name: '  ', value: 'b'},
        {name: '', value: 'p'},
        {name: ' ', value: 'u'},
      ],
      HRLETTER_TYPES: [
        {name: ' ', value: 'p'},
        {name: '', value: 't'},
      ],
      MASKAN_TYPE: [
        {name: '    ', value: true},
        {name: '   ', value: false},
      ],
      OTOMOBIL_TYPE: [
        {name: '    ', value: true},
        {name: '   ', value: false},
      ],

      printUrl: 'payroll/hrletter/all',
      isWorkshopConfirmed: false,
      baseUrl: "payroll/hrletter",
      permissionBasename: "hr_letter",
      appendSlash: true,
      hasList: false,
      hasIdProp: true,
      hasLock: false,
      isDefinable: false,
      myClass: '',
      templates: [],
      sanavat_msg: '  ',
      template: null,
      contract: this.$route.query.contract,
      contracts: [],
      PathLevels,
      VisitorLevels,
      paymentDialog: false,
      payment: '',
      performClearForm: true,
      rules: {
        required: value => !!value || 'Required.',
        min: v => v.length >= 8 || 'Min 8 characters',
      },

    };
  },
  computed: {
    headers() {
    },
  },
  mounted() {
    if (!this.contract) {
      this.request({
        url: this.endpoint(`payroll/contract/`),
        method: "get",
        success: data => {
          console.log(data);
          for (let t in data) {
            this.contracts.push({
              'name': data[t].code + ' ' + data[t].personnel_name + '  ' + data[t].workshop_name,
              'id': data[t].id,
            })
          }
          console.log(this.contracts)
        }
      })
    }
    this.request({
      url: this.endpoint(`payroll/hrletter/templates/`),
      method: "get",
      success: data => {
        console.log(data);
        for (let t in data) {
          this.templates.push({
            'name': data[t].name,
            'info': data[t]
          })
        }
        console.log(this.contracts)
      }
    })
  },

  methods: {
    NumbersOnly(evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault();;
      } else {
        return true;
      }
    },
    to(item) {
      return {
        name: 'HRLetterDetail',
        params: {
          id: item.id,
        },
      };
    },
    show(item) {
      if (item == true) {
        console.log(item)
      }
    },
    unConfirm() {
      this.$router.go()
      this.notify('     ', 'warning')
    },
  },
}
</script>

