<template>
  <v-card>
    <v-card-title>  </v-card-title>
    <v-card-text>
      <m-datatable :headers="headers" :apiUrl="url" :filters.sync="filters" @dblclick:row="(e, row) => $router.push(to(row.item))"
                   ref="datatable">
        <template #item.detail="{ item }">
          <detail-link :to="to(item)" />
        </template>

      </m-datatable>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  name: "TenderList",
  props: {},
  data() {
    return {
      url: "reports/tender/all",
      filters: {},
    };
  },
  computed: {
    headers() {
      return [
        {
          text: "",
          value: "code",
          type: "numeric",

        },
        {
          text: "",
          value: "title",
        },
        {
          text: "",
          value: "explanation",
        },
        {
          text: "",
          value: "province",
        },

        {
          text: "",
          value: "city",
        },
        {
          text: " ",
          value: "bidder",
        },
        {
          text: "   ",
          value: "bidder_postal_code",
          type: "numeric",

        },
        {
          text: "  ",
          value: "bidder_address",
        },


        {
          text: "  ",
          value: "received_deadline",
          type: "date",
        },
        {
          text: "    ",
          value: "send_offer_deadline",
          type: "date",
        },

        {
          text: "   ",
          value: "opening_date",
          type: "date",
        },

        {
          text: "  ",
          value: "offer_expiration",
          type: "date",
        },
        {
          text: "",
          value: "detail",
          sortable: false,
          filterable: false,
        }

      ];
    },
  },
  methods: {
    to(item) {
      return {
        name: "TenderDetail",
        params: {
          id: item.id,
        },
      };
    },

  },
};
</script>

